<template>
  <div>
    <!--  -->
    <el-form :model="baseForm" class="base-form">
      <!--  -->
      <el-form-item>
        <el-input placeholder="url" v-model="baseForm.url" class="input-url" @blur="parseUrl()">
          <el-select
            v-model="baseForm.method"
            slot="prepend"
            placeholder=""
            class="select-method"
          >
            <el-option label="GET" value="get"></el-option>
            <el-option label="POST" value="post"></el-option>
            <el-option label="PUT" value="put"></el-option>
            <el-option label="DELETE" value="delete"></el-option>
          </el-select>
        </el-input>
        <el-button type="primary" @click="executeTest()"></el-button>
      </el-form-item>
      <!--  -->
      <el-form-item>
        <span style="font-size: 1.2em;"></span>
        <el-tabs type="border-card">
          <!-- query  -->
          <el-tab-pane label="Params">
            <span style="margin-right: 1.5em">Query </span>
            <el-button size="mini" icon="el-icon-plus" @click="addKeyValue(0)" circle></el-button>
            <el-table
              :data="params"
              size="mini"
              :header-cell-style="{background:'#eef1f6', color:'#606266', 'text-align': 'center', 'height': '1.2em', 'line-height': '1.2em'}"
            >
              <el-table-column label="KEY" prop="key" width="100px" align="center"></el-table-column>
              <el-table-column label="VALUE" prop="value" align="center"></el-table-column>
              <el-table-column label="" width="150px" align="center">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    type="success"
                    icon="el-icon-edit"
                    circle
                    @click="editKeyValue(scope.$index, 0)"
                  ></el-button>
                  <el-button
                    size="mini"
                    type="danger"
                    icon="el-icon-delete"
                    circle
                    @click="deleteKeyValue(scope.$index)"
                  ></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <!-- body  -->
          <el-tab-pane label="Body">
            <span style="font-size: 1.2em;">Body </span>
            <vueJsonEditor v-model="body" :mode="'code'" lang="zh"></vueJsonEditor>
          </el-tab-pane>
          <!-- header  -->
          <el-tab-pane label="Header">
            <span style="margin-right: 1.5em">Header </span>
            <el-button size="mini" icon="el-icon-plus" @click="addKeyValue(2)" circle></el-button>
            <el-table
              :data="headers"
              size="mini"
              :header-cell-style="{background:'#eef1f6', color:'#606266', 'text-align': 'center', 'height': '1.2em', 'line-height': '1.2em'}"
            >
              <el-table-column label="KEY" prop="key" width="100px" align="center"></el-table-column>
              <el-table-column label="VALUE" prop="value" align="center"></el-table-column>
              <el-table-column label="" width="150px" align="center">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    type="success"
                    icon="el-icon-edit"
                    circle
                    @click="editKeyValue(scope.$index, 2)"
                  ></el-button>
                  <el-button
                    size="mini"
                    type="danger"
                    icon="el-icon-delete"
                    circle
                    @click="deleteKeyValue(scope.$index, 2)"
                  ></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <!-- cookie  -->
          <el-tab-pane label="Cookie">
            <span style="margin-right: 1.5em">Cookie </span>
            <el-button size="mini" icon="el-icon-plus" @click="addKeyValue(3)" circle></el-button>
            <el-table
              :data="cookies"
              size="mini"
              :header-cell-style="{background:'#eef1f6', color:'#606266', 'text-align': 'center', 'height': '1.2em', 'line-height': '1.2em'}"
            >
              <el-table-column label="KEY" prop="key" width="100px" align="center"></el-table-column>
              <el-table-column label="VALUE" prop="value" align="center"></el-table-column>
              <el-table-column label="" width="150px" align="center">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    type="success"
                    icon="el-icon-edit"
                    circle
                    @click="editKeyValue(scope.$index, 3)"
                  ></el-button>
                  <el-button
                    size="mini"
                    type="danger"
                    icon="el-icon-delete"
                    circle
                    @click="deleteKeyValue(scope.$index, 3)"
                  ></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>
      </el-form-item>
      <!--  -->
      <el-from-item v-if="responseCode">
        <br />
        <span style="font-size: 1.2em;"></span>
        <span style="font-size: 0.8em; margin-left: 2em;">: {{ responseCode }}</span>
        <el-tabs type="border-card">
          <!--  header -->
          <el-tab-pane label="Response Header">
            <vueJsonEditor v-model="responseHeader" :mode="'view'" lang="zh"></vueJsonEditor>
          </el-tab-pane>
          <!--  body -->
          <el-tab-pane label="Response Body">
            <vueJsonEditor v-model="responseBody" :mode="'view'" lang="zh"></vueJsonEditor>
          </el-tab-pane>
        </el-tabs>
      </el-from-item>
    </el-form>
    <!--  -->
    <el-dialog
      title=" "
      :visible.sync="dialogVisible"
      width="30em"
      :show-close="false"
      close-on-click-modal
    >
      <el-form :model="tempForm" status-icon size="mini" label-width="20%" class="dialog-form">
        <el-form-item label="KEY: ">
          <el-input v-model="tempForm.key"></el-input>
        </el-form-item>
        <el-form-item label="VALUE: ">
          <el-input type="textarea" rows="3" v-model="tempForm.value"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeDialog()" size="mini"> </el-button>
        <el-button type="primary" @click="saveDialog()" size="mini"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { execute_test } from "@/api";
import vueJsonEditor from "vue-json-editor";

export default {
  data() {
    return {
      baseForm: {},
      params: [],
      headers: [],
      cookies: [],

      // body json
      body: "",
      tempForm: {},
      dialogVisible: false,
      // 
      // 0-query, 1-body, 2-header, 3-cookie
      origin: 0,

      // 
      responseCode: 0,
      responseHeader: "",
      responseBody: ""
    };
  },
  methods: {
    // Query
    addKeyValue(origin) {
      this.dialogVisible = true;
      this.origin = origin;
    },

    // Query
    deleteKeyValue(index, origin) {
      if (origin == 0) {
        this.params.splice(index, 1);
      }
      if (origin == 2) {
        this.headers.splice(index, 1);
      }
      if (origin == 3) {
        this.cookies.splice(index, 1);
      }
    },

    // Query
    editKeyValue(index, origin) {
      this.addKeyValue(origin);
      if (origin == 0) {
        this.tempForm = JSON.parse(
          JSON.stringify(this.params.slice(index, index + 1)[0])
        );
      }
      if (origin == 2) {
        this.tempForm = JSON.parse(
          JSON.stringify(this.headers.slice(index, index + 1)[0])
        );
      }
      if (origin == 3) {
        this.tempForm = JSON.parse(
          JSON.stringify(this.cookies.slice(index, index + 1)[0])
        );
      }
    },

    // 
    closeDialog() {
      this.dialogVisible = false;
      this.tempForm = {};
    },

    // 
    saveDialog() {
      if (this.tempForm.key) {
        // query 
        if (this.origin == 0) {
          this.params.forEach(item => {
            if (item.key == this.tempForm.key) {
              item.value = this.tempForm.value;
              this.tempForm = {};
            }
          });

          if (this.tempForm.key) {
            this.params.push(this.tempForm);
          }
        }

        // header 
        if (this.origin == 2) {
          this.headers.forEach(item => {
            if (item.key == this.tempForm.key) {
              item.value = this.tempForm.value;
              this.tempForm = {};
            }
          });

          if (this.tempForm.key) {
            this.headers.push(this.tempForm);
          }
        }

        // cookie 
        if (this.origin == 3) {
          this.cookies.forEach(item => {
            if (item.key == this.tempForm.key) {
              item.value = this.tempForm.value;
              this.tempForm = {};
            }
          });

          if (this.tempForm.key) {
            this.cookies.push(this.tempForm);
          }
        }
      }
      this.closeDialog();
    },

    // URL
    parseUrl() {
      let tempArray = this.baseForm.url.split("?");
      let url = tempArray[0];

      if (tempArray[1]) {
        this.params = [];
        var queryArray;
        try {
          queryArray = decodeURI(tempArray[1]).split("&");

          queryArray.forEach(item => {
            this.params.push({
              key: item.split("=")[0],
              value: item.split("=")[1]
            });
          });

          this.baseForm.url = url;
        } catch (URIError) {
          this.$message({
            type: "error",
            message: "URL"
          });
        }
      }
    },

    // 
    executeTest() {
      if (this.baseForm.method && this.baseForm.url) {
        execute_test({
          url: this.baseForm.url,
          method: this.baseForm.method,
          params: this.params,
          body: this.body,
          headers: this.headers,
          cookies: this.cookies
        }).then(res => {
          this.responseCode = res.data.code;
          this.responseHeader = res.data.headers;
          this.responseBody = res.data.body;
        });
      } else {
        this.$message({
          type: "error",
          message: ""
        });
      }
    }
  },

  watch: {},

  components: {
    vueJsonEditor
  }
};
</script>
<style scoped>
.base-form {
  width: 70%;
}
.dialog-form {
  width: 90%;
}
.input-url {
  margin-right: 1em;
  width: 80%;
}
.select-method {
  width: 8em;
}
.select-protocol {
  margin-right: 1em;
  width: 6em;
}
</style>    